@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&family=Roboto&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* .navbar-nav{
  margin-left: 40%;
} */


/* font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif; */
.product-link{
  text-decoration: none !important;
  color: black;
}

.navbar{
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  text-transform: uppercase;
  background-color: #fff;
  padding-left: 20px; /* Adjust the left padding to create space for the brand/logo */
  padding-right: 20px; /* Adjust the right padding for spacing on both sides */
  width: 100%;
  display: flex;
  justify-content: space-between; /* Add this to align brand and nav-pills */
  align-items: center; /* Center vertically */
}

.navbar-brand{
  font-weight: 400;
  /* font-family: 'Roboto', sans-serif;  */
  font-family: 'Playfair Display', serif;
  letter-spacing: 1px;
  font-size: 24px; /* Adjust font size as needed */
  margin-right: 200px; /* Add right margin to separate the brand/logo from nav links */
}

/* Add these CSS rules to your stylesheet */
.nav-link {
  text-decoration: none; /* Remove the default underline */
}

/* Underline the active link */
.nav-link.active {
  text-decoration: underline;
}

/* Update the container width to match the browser width */
.container {
  max-width: 100%; /* Set maximum width to 100% to match browser width */
  padding: 0 15px;
  margin: 0 auto;
}

/* Styles specific to the logged-in state */
.logged-in .navbar {
  padding-left: 10px; /* Adjust the left padding when logged in */
  padding-right: 10px; /* Adjust the right padding when logged in */
}

/* Styles specific to the logged-out state */
.logged-out .navbar {
  padding-left: 20px; /* Adjust the left padding when logged out */
  padding-right: 20px; /* Adjust the right padding when logged out */
}

.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title{
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading{
  font-weight: normal;
}

.pnf-btn{
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover{
  background-color: black;
  color: white;
}

.navbar-collapse {
  margin-left: 15px;
}

/* Specific style for .navbar-collapse when the user is logged in */
.logged-in .navbar-collapse {
  margin-left: 3px;
}

/* Specific style for .navbar-collapse when the user is logged out */
.logged-out .navbar-collapse {
  margin-left: 15px;
}

/* Styles specific to the logged-in state */
.logged-in .nav-pills {
  margin-left: auto;
}

.logged-in .nav-link {
  padding: 10px 15px;
  margin-right: -12px; /* Adjust the right margin when logged in */
}

/* Styles specific to the logged-out state */
.logged-out .nav-pills {
  margin-left: auto;
}

.logged-out .nav-link {
  padding: 10px 12px;
  margin-left: 3px; /* Commented out to remove right margin when logged out */
  margin-right: 1px;
}


/* Styles specific to the logged-in state */
