/* Media query for screens smaller than 768px */
@media (max-width: 768px) {
  .navbar-nav {
    flex-direction: column;
    align-items: center;
  }

  .nav-item {
    margin-bottom: 10px;
  }
}


.footer-container {
  width: 100%;
  position: relative;
  background: rgba(0, 0, 0, 0.867);
  min-height: 100px;
  padding: 20px 0; /* Adjust top and bottom padding */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  bottom: 0; /* Position the footer at the bottom of the viewport */
  left: 0;
}

.footer-container .footer-logo{
  position:relative;
  display:flex;
  justify-content:center;
  align-items:center;
  margin:10px 0;
}

.footer-container .menu{
  position:relative;
  display:flex;
  justify-content:center;
  align-items:center;
  margin:12px 0;
  flex-wrap:wrap;
  transition:0.5s;
}

.footer-container .menu li{
  list-style:none;
}

.footer-container .menu li .footer-nav-link{
  font-size:1.2em;
  color:#fff;
  margin:0 10px;
  display:inline-block;
  transition:0.5s;
  text-decoration:none;
  opacity:0.75;
}

.footer-container .menu li .footer-nav-link:hover{
  opacity:1;
  transform:translateY(-10px);
}

.footer-container .footer-statement{
  color:#fff;
  text-align:center;
  margin-top:15px;
  margin-bottom:10px;
  font-size:1.1em;
}

.logged-in .header-short-username {
  /* Rest of your CSS code for length < 6 */
  .navbar-brand {
    /* Existing styles for length < 6 */
    margin-right: 150px; /* Adjust the right margin for short username */
  }
}

.logged-out .header-short-username {
  /* Rest of your CSS code for length < 6 */
  .navbar-brand {
    /* Existing styles for length < 6 */
    margin-right: 190px; /* Adjust the right margin for short username */
  }
}

.logged-in .header-long-username {
  /* Existing styles for length >= 6 */
  .navbar-brand {
    /* Existing styles for length >= 6 */
    margin-right: 150px; /* Adjust the right margin for long username */
  }
}

.logged-out .header-long-username {
  /* Rest of your CSS code for length < 6 */
  .navbar-brand {
    /* Existing styles for length < 6 */
    margin-right: 190px; /* Adjust the right margin for short username */
  }
}

.container-fluid{
  --bs-gutter-x: 0;
}

.nav-link{
  color: blue;
}

.nav-link:hover{
  color: red;
}

.navbar-nav .nav-link.active, .navbar-nav .nav-link.show {
  color: blue;
}

